import React from 'react'
import "../Css/Newsfeed.css"
import Post from './Post'
import { Row , Col } from 'react-bootstrap'
import PostHadhees from './PostHadhees'
import {BrowserRouter as Router,Route,Link}from "react-router-dom";
import Quiz from './Quiz'

function Newsfeed() {
    
    return (
        
        <Row className="testnew justify-content-sm-center">
          <Route path='/notes'><Col sm={12}> {/* <h1>CRE NOTES</h1>*/}</Col><Post></Post></Route> 
          <Route path='/hadheesclass'><Col sm={12}>{/* <h1>HADHEES CLASS</h1>*/}</Col><PostHadhees></PostHadhees></Route>
          <Route path='/quiz'><Col sm={12}>{/* <h1>HADHEES CLASS</h1>*/}</Col><Quiz></Quiz></Route>
           
        </Row>
    )
}

export default Newsfeed
