import React from 'react'
import "../Css/Navibar.css"
import { Button, Row  } from 'react-bootstrap';
import {BrowserRouter as Router,Link,useHistory} from "react-router-dom";
function Navibar() {
    const history = useHistory()
    return (
        <Row className="testnavi justify-content-sm-center">
            
            <div className="box"><img className="crelogo" src="./Images/crelogo.png" alt="" /></div>
            <div className="navilogin"><Button  variant="primary" size="sm" onClick={() =>history.push('/quiz')}>Login</Button><img className="propiclogin" src="./Images/propic.jpg" alt="" /></div>
            
        </Row>
       
    )
}

export default Navibar
