import React, { useState } from 'react';
import { Row , Col, Button } from 'react-bootstrap'
import "../Css/MenuButton.css"
import {BrowserRouter as Router,Link,useHistory} from "react-router-dom";
function MenuButton() {
  const [page, setPage] = useState([])
  const history = useHistory()
    return (
      <Router>
        <Row className="justify-content-sm-center">
          <Col xs={3} className="button1" onClick={() =>history.push('/notes')}><div className="buttontext">CRE NOTES</div></Col>
          <Col xs={3} className="button2" onClick={() =>history.push('/hadheesclass')}><div className="buttontext">Hadhees CLASS</div></Col>
          <Col xs={3} className="button3" onClick={() =>history.push('/quiz')}><div className="buttontext">Quiz</div></Col>
        </Row>
        </Router> 
    )
}

export default MenuButton
